import React from 'react'
import "../style.css";


import { 
    InfoSection, 
    InfoContainer, 
    InfoColumnLeft, 
    InfoColumnRight,
    InfoButton} 
from './InfoElements';


const Info = ({ 
    heading, 
    paragraphOne, 
    paragraphTwo, 
    buttonLabel, 
    reverse,
    image,
    link,
    id
}) => {
    return (
        <InfoSection id={id}>
            <InfoContainer>
                <InfoColumnLeft >
                    <h1>{heading}</h1>
                    <p>{paragraphOne}</p>
                    <p>{paragraphTwo}</p>
                    <InfoButton target="_blank" href={link} className="ButtonLink">{buttonLabel}</InfoButton>
                </InfoColumnLeft>
                <InfoColumnRight reverse={reverse}>
                    <img src={image} alt='cachoeira' />
                </InfoColumnRight>
            </InfoContainer>
        </InfoSection>
    );
};


export default Info;
