import React, {useState} from 'react';
import NavBar from './components/NavBar';
import Dropdown from './components/Dropdown';
import Hero from './components/Hero';
import Info from './components/Info';

import Feature from './components/Feature';
import Feature2 from './components/Feature2';
import Feature3 from './components/Feature3';
import Footer from './components/Footer';

import { InfoData } from './data/InfoData';
import { InfoDataTwo } from './data/InfoData';
import { InfoDataThree } from './data/InfoData';
import { InfoDataFour } from './data/InfoData';
import { InfoDataFive } from './data/InfoData';

import { SliderData } from './data/SliderData';
import GlobalSyle from './globalStyles';
function App() {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => {
    setIsOpen(!isOpen);
  };

  return (
   <>
    <GlobalSyle />
    <NavBar toggle={toggle} />
    <Dropdown isOpen={isOpen} toggle={toggle} />
    <Hero slides={SliderData}/>
    <Info {...InfoData}/>
    <Feature {...InfoDataTwo}/>
    <Info {...InfoDataThree}/>
    <Feature2 {...InfoDataFour }/>
    <Info {...InfoDataFive}/>
    <Feature3 />
    <Footer />
   </>
  );
}

export default App;
