import styled from 'styled-components'
import { Link } from 'react-router-dom'

export const Button = styled(Link) `
    background: ${({primary}) => (primary ? '#000d1a' : '#ffc500')};
    white-space: nowrap;
    outline: none;
    border: none;
    min-width: 100px;
    max-width: ${({max}) => (max ? '200px' : '160px')};
    cursor: pointer;
    text-decoration: none;
    transition: 0.3s;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: ${({big}) => (big ? '15px 30px' : '10px 12px')};
    color: ${({primary}) => (primary ? '#fff' : '#000d1a')};
    font-size: ${({big}) => (big ? '14px' : '10px')};

    &:hover {
        transform: translateY(-2px);
        color: ${({primary}) => (primary ? '#000d1a' : '#fff')};
        background: ${({primary}) => (primary ? '#ffc500' : '#000d1a')};
        transition: 0.2s ease-out;
        cursor: pointer;
    }
`;