import React from 'react'
import { menuData } from '../../data/MenuData'
import { Button } from '../Button'
import { Link } from 'react-scroll';

import { 
    DropdownContainer, 
    CloseIcon, 
    DropdownWrapper, 
    DropdownMenu,
    DropdownLink,
    BtnWrap,
    LinkMenu} 
from './DropdownElements';


const Dropdown = ({isOpen, toggle}) => {
    return (
        <DropdownContainer isOpen={isOpen} onClick={toggle}>
            <CloseIcon onClick={toggle}/>
            <DropdownWrapper>
                <DropdownMenu>
                    <DropdownLink>
                        <Link to='sobre' smooth={true} duration={1000} className="LinkMenu" onClick={toggle}>
                        Sobre
                        </Link>
                    </DropdownLink>

                    <DropdownLink>
                        <LinkMenu href='https://www.google.com/maps/place/Pres.+Figueiredo,+AM,+69735-000/@-2.0300068,-60.0435571,14z/data=!3m1!4b1!4m5!3m4!1s0x92720580466a7f77:0xf8beed823c8c5185!8m2!3d-2.0300069!4d-60.0260475' 
                            target='_blank' aria-label='Lugar' className="LinkMenu" onClick={toggle}> 
                        Lugar
                        </LinkMenu>
                    </DropdownLink>

                    <DropdownLink>
                        <Link to='iracema' smooth={true} duration={1000} className="LinkMenu" onClick={toggle}>
                        Cachoeiras
                        </Link>
                    </DropdownLink>
                </DropdownMenu>
                <BtnWrap>
                    <Button primary='true' round='true' big='true' max='true' to="/contato"  >
                        Contate-nos
                    </Button>
                </BtnWrap>
            </DropdownWrapper>
        </DropdownContainer>
        )
    };
export default Dropdown;
