import React from 'react';
import { Link } from 'react-scroll';
import "../style.css";

import { 
    Nav, 
    Logo, 
    MenuBars, 
    NavMenu,
    NavMenuLinks,
    NavBtn,
    LinkMenu} 
from './NavBarElements';

const NavBar = ({toggle}) => {
    return (
        <Nav>
            <Logo to='/'>Presidente Figueiredo</Logo>
            <MenuBars onClick={toggle} />
            <NavMenu>
                <NavMenuLinks>
                    <Link to='sobre' smooth={true} duration={1000} className="Link">
                    Sobre
                    </Link>
                    <LinkMenu href='https://www.google.com/maps/place/Pres.+Figueiredo,+AM,+69735-000/@-2.0300068,-60.0435571,14z/data=!3m1!4b1!4m5!3m4!1s0x92720580466a7f77:0xf8beed823c8c5185!8m2!3d-2.0300069!4d-60.0260475' 
                        target='_blank' aria-label='Lugar' className="Link"> 
                    Lugar
                    </LinkMenu>
                    <Link to='iracema' smooth={true} duration={1000} className="Link">
                    Cachoeiras
                    </Link>
                </NavMenuLinks>
            </NavMenu>
            <NavBtn>
                <Link to='footer' smooth={true} duration={1000} className="ButtonLink">
                    Contate-nos
                </Link>
            </NavBtn>
        </Nav>
    );
};

export default NavBar;