import styled from 'styled-components'
import { Link } from 'react-router-dom'
import Bars from '../../images/bars.svg';

export const Nav = styled.nav`
    height: 60px;
    display: flex;
    justify-content: space-between;
    padding: 1rem 2rem;
    z-index: 100;
    position: fixed;
    width: 100%;
    background-color: #c22136;
`;

export const NavLink = {
    color: '#fff',
    display: 'flex',
    alignItems: 'center',
    padding: '0 1rem',
    height: '100%',
    cursor: 'pointer',
    textDecoration: 'none',
    fontSize: '14px'
};

export const Logo = styled(Link)`
    ${NavLink};
    font-style: italic;
    font-size: 1rem;
    font-weight: bold;
`;

export const MenuBars = styled.i`
    display: none;
    background-image: url(${Bars});
    background-size: contein;
    height: 40px;
    width: 40px;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-50%, 25%);


    @media(max-width: 768px) {
        display: block;
    }
`;

export const NavMenu = styled.div`
    display: flex;
    align-items: center;
    margin-right: -48px;

    @media(max-width: 768px) {
        display: none;
    }
`;

export const NavMenuLinks = styled.a`
    ${NavLink}
`;

export const NavBtn = styled.div`
    display: flex;
    align-items: center;
    margin-right: 24px;

    @media(max-width: 768px) {
        display: none;
    }
`;

export const LinkMenu = styled.a``;


