import styled from 'styled-components/macro'
import {IoMdArrowRoundForward} from 'react-icons/io'
import {IoArrowBack, IoArrowForward} from 'react-icons/io5'

export const HeroSection = styled.section`
    height: 100vh;
    max-height 1100px;
    position: relative;
    overflow: hidden;
`;

export const HeroWrapper = styled.div `
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;
`;

export const HeroSlide = styled.div`
    z-index: 1;
    width: 100%;
    height: 100%;
`;

export const HeroSlider = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    &::before{
        content: '';
        position: absolute;
        z-index: 2;
        width: 100%;
        height: 100vh;
        bottom: 0vh;
        left:0;
        overflow: hidden;
        opacity: 0.8;
        background: linear-gradient(
            0deg, 
            rgba(0,0,0,0.2) 0%,
            rgba(0,0,0,0.4) 50%,
            rgba(0,0,0,0.8) 100%
        );
    }
`;

export const HeroImage = styled.img`
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
`;

export const HeroContent = styled.div`
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    max-width: 1600px;
    width: calc(100% - 100px);
    color: #fff;

    h1 {
        font-size: clamp(2.5rem, 2vw, 3rem);
        font-weight: BOLD;
        text-transform: upppercase;
        text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
        text-align: left;
        margin-bottom: 0.8rem;
    }

    p {
        font-weight: 400;
        margin-bottom: 1.2rem;
        text-shadow: 0px 0px 30px rgba(0, 0, 0, 0.4);
    }
`;


export const Arrow = styled(IoMdArrowRoundForward)`
    margin-left: 0.5rem;
`;

export const SliderButtons = styled.div`
    position: absolute;
    bottom: 50px;
    right: 50px;
    display: flex;
    z-index:10;
`;

export const arrowButtons = {
    width: '50px',
    height: '50px',
    color: '#fff',
    cursor: 'pointer',
    background: '#000d1a',
    borderRadius: '50px',
    padding: '10px',
    marginRight: '1rem',
    userSelect: 'none',
    transition: '0.3s',

    '&:hover':{
        background: '#ffc500',
        transform: 'scale(1.05)'
    }
};

export const PrevArrow = styled(IoArrowBack)`
    ${arrowButtons}
`;

export const NextArrow = styled(IoArrowForward)`
    ${arrowButtons}
`;


